<template>
  <div class="login">
    <div class="container">
      <div class="title">
        <h1>管理系统</h1>
      </div>
      <div class="input">
        <el-form
          ref="form"
          :model="loginForm"
          :rules="rules"
          :show-message="false"
          class="input-form"
          @validate="validate"
        >
          <el-form-item
            class="form-item"
            prop="userName"
          >
            <el-input
              class="input-item"
              v-model="loginForm.userName"
              placeholder="请输入用户名"
              autofocus
            >
              <template slot="prepend"><i class="el-icon-user-solid"></i></template>
            </el-input>
          </el-form-item>
          <el-form-item
            class="form-item"
            prop="password"
          >
            <el-input
              class="input-item"
              type="password"
              v-model="loginForm.password"
              placeholder="请输入密码"
              @keyup.native.enter="login"
            >
              <template slot="prepend"><i class="el-icon-lock"></i></template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              class="form-button"
              type="primary"
              @click="login"
            >登录</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data () {
    return {
      loginForm: {
        userName: "",
        password: ""
      },
      rules: {
        userName: [
          { required: true, message: "请输入用户名", trigger: "blur" }
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }]
      }
    };
  },
  methods: {
    validate (name, valid, message) {
      if (!valid) {
        this.$message({
          showClose: true,
          message: message,
          type: "error"
        });
      }
    },
    login () {
      this.$refs.form.validate(valid => {
        if (valid) {
          console.dir(this.loginForm);
          this.$router.push("app");
        }
      });
    }
  }
};
</script>

